.gallery {
   position: relative;
   background: linear-gradient(to bottom, #FFF5F6 43%, #fff 43%);
}
.gallery__container{
   display: flex;
   width:  100%;
   height: 100%;
   align-items: center;
   justify-content: center;
   position: absolute;
   top: 0;
   left: 0;
}
.gallery__img {
   cursor: help;
   border-radius: 20px;
   width: 100%;
   height: 100%;
   object-fit: cover;
   object-position: center;
   top: 0;
   left: 0;
   transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
}
.gallery__img:hover,
.gallery__img:focus{
   transform: scale(1.4);
   transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
}
.gallery__img:active{
   cursor: zoom-in;
   transform: scale(2.5);
   transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1),
               background-color 350 mscubic-bezier(0.4, 0, 0.2, 1);
}
@media screen and (max-width: 768px){
